import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { PayPalButton } from 'react-paypal-button-v2'
import { Link } from 'react-router-dom'
import { Row, Col, ListGroup, Image, Card, Button } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import Message from '../components/Message'
import Loader from '../components/Loader'
import Meta from '../components/Meta'
import { getOrderDetails, payOrder, deliverOrder } from '../actions/orderActions'
import { ORDER_PAY_RESET, ORDER_DELIVER_RESET } from '../constants/orderConstants'

const OrderScreen = ({ match, history }) => {
	const orderId = match.params.id

	const [sdkReady, setSdkReady] = useState(false)
	
	const dispatch = useDispatch()

	const orderDetails = useSelector(state => state.orderDetails)
	const { order, loading, error } = orderDetails

	const orderPay = useSelector(state => state.orderPay)
	const { loading: loadingPay, success: successPay } = orderPay

	const orderDeliver = useSelector(state => state.orderDeliver)
	const { loading: loadingDeliver, success: successDeliver } = orderDeliver

	const userLogin = useSelector(state => state.userLogin)
	const { userInfo } = userLogin

	// Calculate prices
	if(!loading) {
		const addDecimals = (num) => {
			return (Math.round(num * 100) / 100).toFixed(2)
		}

		order.itemsPrice = addDecimals(order.orderItems.reduce((acc, item) => acc + item.price * item.qty, 0))
	}

	useEffect(() => {
		if(!userInfo) {
			history.push('/login')
		} 
		
		const addPayPalScript = async () => {
			const { data: clientId } = await axios.get('/api/config/paypal')
			const script = document.createElement('script')
			script.type = 'text/javascript'
			script.src = `https://www.paypal.com/sdk/js?client-id=${clientId}`
			script.async = true
			script.onload = () => {
				setSdkReady(true)
			}
			document.body.appendChild(script)
		}

		if(!order || successPay || successDeliver ||order._id !== orderId) {
			dispatch({ type: ORDER_PAY_RESET })
			dispatch({ type: ORDER_DELIVER_RESET })
			dispatch(getOrderDetails(orderId))
		} else if(!order.isPaid) {
			if(!window.paypal) {
				addPayPalScript()
			} else {
				setSdkReady(true)
			}
		}
	}, [dispatch, history, userInfo, orderId, successPay, successDeliver, order])

	const successPaymentHandler = (paymentResult) => {
		dispatch(payOrder(orderId, paymentResult))
	}

	const successDeliveryHandler = (DeliveryResult) => {
		dispatch(deliverOrder(order, DeliveryResult))
	}

	return loading ? <Loader /> : error ? <Message variant='danger'>{error}</Message> : <>
		<Meta title='Order | Roots' />
		<h1>Thanks for your order!</h1>
		<Row>
			<Col md={8}>
				<ListGroup variant='flush'>
					<ListGroup.Item>
						<h2>Shipping</h2>
						<ListGroup variant='flush'>
								<ListGroup.Item>
									<Row>
										<Col md={2}>
											<p><strong>Name</strong></p>
											<p><strong>Email</strong></p>
											<p><strong>Address</strong></p>	
										</Col>
										<Col>
											<p>{order.user.name}</p>
											<p><a href={`mailto:${order.user.email}`}>{order.user.email}</a></p>
											<p>{order.shippingAddress.address},{' '}{order.shippingAddress.city}{' '}{order.shippingAddress.postalCode},{' '}{order.shippingAddress.country}</p>
										</Col>
									</Row>
									{order.isDelivered ? <Message variant='success'>Delivered on {order.deliveredAt.substring(0, 10)}</Message> : <Message variant='danger'>Not delivered</Message>}
								</ListGroup.Item>
						</ListGroup>
					</ListGroup.Item>

					<ListGroup.Item>
						<h2>Payment Method</h2>
						<ListGroup variant='flush'>
							<ListGroup.Item>
								<Row>
									<Col md={2}>
										<p><strong>Method</strong></p>
									</Col>
									<Col>
										<p>{order.paymentMethod}</p>
									</Col>
								</Row>
								{order.isPaid ? <Message variant='success'>Paid on {order.paidAt.substring(0, 10)}</Message> : <Message variant='danger'>Not paid</Message>}
							</ListGroup.Item>
						</ListGroup>
					</ListGroup.Item>

					<ListGroup.Item>
						<h2>Order Details</h2>
						{order.orderItems.length === 0 ? <Message>Order is empty</Message> : (
							<ListGroup variant='flush'>
								{order.orderItems.map((item, index) => (
									<ListGroup.Item key={index}>
										<Row>
											<Col md={1}>
												<Image src={item.image} alt={item.name} fluid rounded />
											</Col>
											<Col>
												<Link to={`/product/${item.product}`}>
													{item.name}
												</Link>
											</Col>
											<Col md={4}>
												{item.qty} x ${item.price} = ${item.qty * item.price}
											</Col>
										</Row>
									</ListGroup.Item>
								))}
							</ListGroup>
						)}
					</ListGroup.Item>
				</ListGroup>
			</Col>
			<Col md={4}>
				<Card>
					<ListGroup variant='flush'>
						<ListGroup.Item>
							<h2>Order Summary</h2>
						</ListGroup.Item>
						<ListGroup.Item>
							<Row>
								<Col>Items</Col>
								<Col>${order.itemsPrice}</Col>
							</Row>
						</ListGroup.Item>
						<ListGroup.Item>
							<Row>
								<Col>Shipping</Col>
								<Col>${order.shippingPrice}</Col>
							</Row>
						</ListGroup.Item>
						<ListGroup.Item>
							<Row>
								<Col>Tax</Col>
								<Col>${order.taxPrice}</Col>
							</Row>
						</ListGroup.Item>
						<ListGroup.Item>
							<Row>
								<Col>Total</Col>
								<Col>${order.totalPrice}</Col>
							</Row>
						</ListGroup.Item>
						{!order.isPaid && (
							<ListGroup.Item>
								{loadingPay && <Loader />}
								{!sdkReady ? <Loader /> : (
									<PayPalButton amount={order.totalPrice} onSuccess={successPaymentHandler} />
								)}
							</ListGroup.Item>
						)}
						{loadingDeliver && <Loader />}
						{userInfo && userInfo.isAdmin && order.isPaid && !order.isDelivered && (
							<ListGroup.Item>
								<Button type='button' className='btn btn-block' onClick={successDeliveryHandler}>
									Mark As Delivered
								</Button>
							</ListGroup.Item>
						)}
					</ListGroup>
				</Card>
			</Col>
		</Row>
	</>
}

export default OrderScreen
